/* Chrome, Safari, Edge, Opera */
input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

/* Firefox */
input[type=number] {
  -moz-appearance: textfield;
}
*{
  margin: 0;
  padding: 0;
  box-sizing: border-box
}
body{
}
.head{
  position: fixed;
  z-index: -1;
  height: 100%;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
  background: #5d380022;
}
.face{
  position: fixed;
  width: 300px;
  height: 300px;
  border-radius: 50%;
  background: #ffcd0044;
  display: flex;
  margin: auto;
  justify-content: center;
  align-items: center
}
.face::before{
  content: '';
  position: absolute;
  top: 180px;
  width: 150px;
  height: 70px;
  background: #b5770044;
  border-bottom-left-radius: 70px;
  border-bottom-right-radius: 70px;
  z-index: 15;
  transition: 0.5s
}
.face:hover::before{
  top: 210px;
  width: 150px;
  height: 20px;
  background: #b5770044;
  border-bottom-left-radius: 0px;
  border-bottom-right-radius: 0px;
  z-index: 15;
}
.eyes{
  position: relative;
  top: -40px;
  display: flex
}
.eyes .eye{
  position: relative;
  width: 80px;
  height: 80px;
  display: block;
  background: #fff4;
  margin: 0 15px;
  border-radius: 50%
}
.eyes .eye::before{
  content: '';
  position: absolute;
  top: 50%;
  left: 25px;
  transform: translate(-50%, -50%);
  width: 40px;
  height: 40px;
  background: #000;
  border-radius: 50%
}

tr{
  background: #FFF8
}

.ok:last-of-type{
 display: none
}
